  position: absolute;
  top: 0;
  left: 0;
  height: calc(100% - 100px);
  width: calc(100% - 100px);
  padding: 50px;
  color: #fff;
  background: rgba(0, 0, 0, 0.4);
  text-shadow: 2px 2px 2px #000;
}
.logo{
  width: 120px;
  margin-top: -20px;
  margin-right: -50px;
}
.info-logo {
  width: 150px;
  margin: 0 auto;
  display: block;
  padding-bottom: 40px;
}
.clock{
  font-size: 3em
}
@media only screen and (max-width: 725px) {
  #info{
    padding: 10px;
    height: calc(100% - 20px);
    width: calc(100% - 20px);
  }
  .info-logo {
    width: 50px;
    padding-bottom: 10px;
  }
}
.laser{
  width: 10px;
  position: absolute;
  bottom: 100px;
  left: 50%;
